.Input {
  position: relative;
}
.Input .Input__Label {
  font-size: 12px;
  color: #b2b2b2;
}
.Input .Error {
  color: #ed3035;
}
.Input .Error--Border {
  border: 1px solid #ed3035;
}
.Input .ant-input {
  border-radius: 4px;
  font-size: 0.875rem;
}
.Input .ant-input:focus {
  border: 1px solid #4c4c4c;
  outline: 1;
  -webkit-box-shadow: 0 2px 14px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 14px 0 rgba(0, 0, 0, 0.1);
  border-right-width: 1px !important;
}
.Input .Input__Error {
  font-size: 0.75rem;
  position: absolute;
  bottom: -22px;
  left: 0;
  color: #ed3035;
}
